.feed {
  display: flex;
  padding: 4rem;
  justify-content: space-around;
  flex-wrap: wrap;
  background: #ffffff;
 
}


@media only screen and (max-width: 500px) {
  .feed {
    padding: 2rem;
  }
}

@media only screen and (max-width: 400px) {
  .feed {
    padding: .8rem;
  }
}